{
    display:block;
    margin-bottom: 20px;
    padding: 4px ;
    box-sizing: border-box ;
    height: 32px;
    width: 220px;
    border:none;
    outline:none;
    border-bottom: solid 1px;
    font-family: sans-serif;
    font-size: 15px;
    font-weight: 400;
    transition: 0.2s ease;
}
input[type="text"]
{
    display:block;
    margin-bottom: 20px;
    padding: 4px ;
    box-sizing: border-box ;
    height: 32px;
    width: 220px;
    border:none;
    outline:none;
    border-bottom: solid 1px;
    font-family: sans-serif;
    font-size: 15px;
    font-weight: 400;
    transition: 0.2s ease;
}
input[type="Submit"]
{
    border-radius: 2%;
    -webkit-transform: uppercase;
            transform: uppercase;
    transition: 0.2s ease;
    background: #dd4400;
    font-weight: 400px;
    text-transform: uppercase;
    cursor: pointer;
    margin-top: 10px;

}
input[type="Submit"]:hover
input[type="Submit"]:focus
{
    background: white;
    transition: 0.2 ease;
}
